import React from "react";
import { Link } from "gatsby";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles({
  avatarMargin: {
    marginRight: '16px',
    '@media (max-width:960px)': {
      marginRight: '8px',
    }
  },
  boxMargin: {
    marginLeft: '16px',
    '@media (max-width:960px)': {
      marginLeft: '8px',
    }
  },
  linkMargin: {
    marginRight: '16px',
    '@media (max-width:960px)': {
      marginRight: '8px',
    }
  }
});

const linkStyle = {
  color: "black",
  textDecoration: "none",
  paddingBottom: '2px',
};

const Header = ({ imageSize = 60 }) => {
  const classes = useStyles();

  return (
    <AppBar position="static" color="default" style={{ backgroundColor: '#f5f5f5', borderBottom: '1px solid black' }} elevation={0}>
      <Toolbar style={{ padding: '17px 0', minHeight: 'auto' }}>
        {/* Left Side */}
        <Box display="flex" alignItems="center" flexGrow={1} className={classes.boxMargin}>
          <Link 
            to="/" 
            style={linkStyle}
            onMouseEnter={(e) => e.target.style.textDecoration = 'underline'}
            onMouseLeave={(e) => e.target.style.textDecoration = 'none'}
          >
            <Typography variant="h6">
              Gaurav
            </Typography>
          </Link>
        </Box>

        {/* Right Side */}
        <Link 
            to="/about" 
            style={linkStyle}
            className={classes.linkMargin}
            onMouseEnter={(e) => e.target.style.textDecoration = 'underline'}
            onMouseLeave={(e) => e.target.style.textDecoration = 'none'}
        >   
            <Typography variant="h6">  {/* You can adjust the variant as needed */}
              About
            </Typography>
        </Link>
      </Toolbar>
    </AppBar>
  );
};

export default Header;
